.seatItem {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 100%;
    font-size: 14px;
    line-height: 1;
    color: #FFFFFF;
    cursor: pointer;
    border-radius: 3px;
    background-color: #CCCCCC;
    -webkit-transition: background-color .2s ease;
    -o-transition: background-color .2s ease;
    transition: background-color .2s ease;
}

.seatItem_status_vacant {
    background-color: #27ae60;
}

.seatItem_status_vacant:hover {
    background-color: #2ecc71;
}

.seatItem_status_booked {
    background-color: #e74c3c;
    cursor: not-allowed;
}

.seatItem_status_ordered {
    background-color: #f1c40f;
}



